/* general */
html,
body {
  overscroll-behavior-y: contain;
}
body {
  font-family: "Helvetica", Times, "Times New Roman", serif;
  min-height: unset!important;
  /* https://github.com/rstudio/shinydashboard/issues/283 */
}
header.main-header {
  width: 100vw;
}
.miro-noscript {
  position: absolute;
  width: 100%;
  text-align: center;
  top: 40%;
  font-weight: bold;
  font-size: 14pt;
}
.btn-default {
  border: 2px solid #ADADAD;
  background-color: rgba(0, 0, 0, 0);
  transition: all 500ms ease;
}
.btn-custom {
  border-width: 1px;
  box-shadow: rgba(0, 0, 0, 0.08) 0px 1px 4px;
}
.readme-wrapper {
  max-height: 65vh;
  overflow: auto;
  position: relative;
}
.readme-wrapper table {
  margin: 10px 0;
}
.readme-wrapper td {
  padding: 10px;
}
.readme-wrapper tr:nth-child(even) {
  background-color: #f2f2f2;
}
.readme-wrapper tr:nth-child(odd) {
  background-color: #fff;
}
.readme-wrapper tr:hover {
  background-color: #ddd;
}
.readme-wrapper th {
  position: -webkit-sticky;
  position: sticky;
  z-index: 500;
  padding-top: 15px;
  padding-bottom: 10px;
  background: #fff;
}
.readme-wrapper code {
  color: var(--primary-color);
  background-color: var(--if-8e425bfe);
}
.readme-preview-markdown {
  width: 100%;
  height: 65vh;
  resize: none;
  background-color: #efefef;
}
textarea.readme-preview-markdown:focus {
  outline-color: var(--primary-color);
}
.readme-preview-output {
  height: 65vh;
  border: 1px solid #ADADAD;
}
.readme-preview-header {
  font-size: 22px;
  padding-bottom: 5px;
}
@media only screen and (max-width: 767px) {
  .katex-display {
    display: inline-block;
    max-width: 100%;
    overflow-x: scroll;
    overflow-y: hidden;
  }
  .katex-display::-webkit-scrollbar {
    height: 5px;
  }
  .katex-display > .katex {
    display: inline-block;
    white-space: nowrap;
    text-align: initial;
  }
  .katex {
    font: normal 1.21em KaTeX_Main, Times New Roman, serif;
    line-height: 1.2;
    white-space: normal;
    text-indent: 0;
  }
}
.btn-default:hover {
  color: #000;
}
.err-msg,
.config-array-err {
  margin: 5px;
  font-weight: bold;
  overflow-wrap: break-word;
  color: var(--secondary-color);
}
.input-validation-error {
  cursor: default;
  max-height: 110px;
  overflow: auto;
  padding-left: 20px;
}
.flex-design {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}
.ttip {
  position: relative;
  text-decoration: underline;
  text-decoration: underline dotted #000;
}
.ttip span {
  visibility: hidden;
  position: absolute;
  z-index: 999;
  padding: 5px 10px 5px 10px;
  border-radius: 5px;
  color: #ffffff;
  background: #000000;
  opacity: 0.9;
  bottom: 70%;
  left: 50%;
  width: 200px;
  margin-left: -100px;
  text-align: center;
}
.ttip:hover span {
  visibility: visible;
}
.ttip span:after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  width: 0;
  height: 0;
  margin-left: -5px;
  border-top: 5px solid #000000;
  border-right: 5px solid transparent;
  border-left: 5px solid transparent;
}
::selection {
  color: #fff;
  background: var(--primary-color);
}
::-moz-selection {
  color: #fff;
  background: var(--primary-color);
}
.box-header ::selection {
  background: var(--sidebar-color);
}
.box-header ::-moz-selection {
  background: var(--sidebar-color);
}
#shiny-tab-gamsinter .box-header ::selection {
  background: var(--sidebar-color);
}
#shiny-tab-gamsinter .box-header ::-moz-selection {
  background: var(--sidebar-color);
}
.gmsdialog-text {
  white-space: pre-wrap;
  max-height: 500px;
  max-height: 50vh;
  overflow: auto;
}
/* custom alerts */
.gmsalert {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  text-align: center;
  max-width: 400px;
  display: none;
  font-weight: 600;
  padding: 15px;
  z-index: 999;
  border: 1px solid #000;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}
.gmsalert-success {
  color: #fff;
  background-color: var(--primary-color);
}
.gmsalert-error {
  background-color: var(--secondary-color);
  color: var(--if-6f85b072);
}
.center-alert {
  position: fixed;
  top: 20%;
  left: 50%;
  right: unset;
  transform: translate(-50%, -50%);
}
.invalidInput {
  border: 2px solid var(--secondary-color);
}
/* title field in boxes */
.box-title {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.inline-el {
  display: inline;
}
.progress-bar {
  background-color: var(--secondary-color);
  color: var(--if-6f85b072);
}
.no-animations {
  -webkit-animation: none !important;
  -moz-animation: none !important;
  -o-animation: none !important;
  -ms-animation: none !important;
  animation: none !important;
}
.loading-input {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -16px;
  /* half of the spinner's height */
  margin-left: -16px;
  /* half of the spinner's width */
}
.loading-input-l {
  position: absolute;
  left: 25%;
  top: 50%;
}
.loading-input-r {
  position: absolute;
  left: 75%;
  top: 50%;
}
.renderer-wrapper {
  position: relative;
}
.renderer-wrapper .lds-ellipsis {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: -1;
  margin-top: -33px;
  /* half of the spinner's height */
  margin-left: -33px;
  /* half of the spinner's width */
}
.renderer-wrapper .recalculating {
  z-index: -2;
}
.render-output .plot-container {
  /* make plotly chart height responsive. See also https://community.plotly.com/t/plot-sizing-problems/1620/24 */
  height: 450px;
  height: 65vh;
}
@media (max-width: 991px) {
  .dtgraph-wrapper > .dtgraph-graph {
    margin-bottom: 50px;
  }
}
.datepicker {
  /* bring datepicker menu to the front */
  z-index: 9999 !important;
}
.label-class {
  cursor: default;
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: 700;
}
.control-label {
  width: 100%;
}
.attachment-line .shiny-input-container {
  margin: 0;
}
.attachment-line .checkbox {
  margin: 5px;
}
/* header */
.main-header .logo {
  background-size: cover;
  background-repeat: no-repeat;
  font-size: 0;
}
.skin-black .main-header .logo {
  background-image: url('../../../8_miro/images/miro_logo_light.png');
}
.menu-icon-align {
