}
.bt-highlight-2 {
  border: 2px solid var(--secondary-color);
  border-color: var(--if-10618845);
  background-color: var(--if-4ab14308);
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
  transition: all 350ms ease;
}
.bt-highlight-2:hover {
  border: 2px solid #ADADAD;
}
.bt-highlight-3 {
  border: 2px solid #ADADAD;
  border-color: var(--if-1b7f3b8b);
  background-color: var(--if-4ab14308);
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
  transition: all 350ms ease;
}
.bt-highlight-3:hover {
  border: 2px solid var(--secondary-color);
}
.bt-remove {
  border: 2px solid var(--alert-color);
}
/* model input */
.bt-icon {
  padding: 0;
  border: none;
  background-color: inherit !important;
}
.bt-icon:hover,
.bt-icon:active {
  background-color: inherit !important;
